<template>
  <div class="suggestion-modal">
    <vpo-button v-if="drawer" text="sugerir" @click="isModalOpen = true" />
    <a v-else-if="text" @click="isModalOpen = true">vai de formulrio</a>
    <vpo-button v-else :dark="!outlined" :outlined="outlined" text="mandar uma ao" @click="isModalOpen = true" />

    <modal v-model="isModalOpen" title="mandar uma ao" action="enviar" :isActionLoading="$store.state.feedback.sending" @confirm="send">
      <div class="modal-content">
        <p>
          <b>Sentiu falta de alguma coisa?</b> Se sabe de algo que vai acontecer, ou se promove alguma ao,
          contedo, evento ou produto que pode facilitar a quarentena de outras pessoas,  s mandar aqui que
          colocamos no site! (Todos os campos so obrigatrios)
        </p>
        <p>
          Tipo de ao
          <span>Escolha o tipo que mais se encaixa</span>
        </p>
        <chooser v-model="suggestion.type" :options="chooserOptions" />
        <vpo-input v-model="suggestion.title" placeholder="Ttulo da ao" />
        <vpo-input v-model="suggestion.brand" placeholder="Responsvel pela ao" label="Nome da pessoa, grupo, coletivo ou marca promovendo a ao" />
        <vpo-input v-model="suggestion.link" placeholder="Link de acesso" label="Como fazemos para acessar a ao?" />
        <vpo-input v-model="suggestion.description" placeholder="Descrio" />
        <vpo-input v-model="suggestion.validUntil" placeholder="Ao vlida at" label="Em caso de festival, colocar o ltimo dia" optional />
      </div>
    </modal>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  name: 'suggestion-modal',
  props: {
    outlined: Boolean,
    drawer: Boolean,
    text: Boolean
  },
  data: () => ({
    isModalOpen: false,
    suggestion: {
      type: 'learn',
      title: '',
      brand: '',
      link: '',
      description: '',
      validUntil: ''
    }
  }),
  computed: {
    chooserOptions() {
      return [
        {
          value: 'learn',
          text: 'Aprender',
          label: 'Sugestes de cursos, palestras ou mentorias disponveis gratuitamente',
          type: 'learn'
        }, {
          value: 'participate',
          text: 'Participar',
          label: 'Lives, congressos, festivais, shows mas tem que ser online, hein?',
          type: 'participate'
        }, {
          value: 'do',
          text: 'Fazer',
          label: 'Sugestes de atividades e brincadeiras, apps liberados de graa',
          type: 'do'
        }, {
          value: 'share',
          text: 'Compartilhar',
          label: 'Sites informativos, canais do YouTube, perfil do instagram, blogs...',
          type: 'share'
        }
      ]
    }
  },
  methods: {
    ...mapActions({ sendSuggestion: 'feedback/sendSuggestion' }),
    async send() {
      try {
        await this.sendSuggestion(this.suggestion)
      } catch {}

      this.isModalOpen = false
    }
  }
}
</script>

