      & > span {
        display: block;
        font-size: 9px;
        font-weight: normal;
      }
    }

    & > .chooser {
      margin-bottom: 16px;
    }

    @include breakpoint('extra-small') {
      & > p:not(:first-child) {
        margin-top: 10px;

        & > span {
          font-size: 11px;
        }
      }
    }

    @include breakpoint('small') {
      & > p:not(:first-child) {
        margin-bottom: 4px;

        & > span {
